<template>
    <nav class="sidebar-menu">
        <p class="sidebar-close" @click="closeModal">
            <i class="fal fa-times-circle"></i>
        </p>
        <div class="sidebar-profile">
            <img src="https://www.billiardport.com/assets/pages/media/profile/profile_user.jpg" class="profile">
            <div class="sidebar-info">
                <div class="box-btn-profile" style="margin-top: 20px;margin-left: 25px">
                    <button class="button" @click="ToMyProfle('seeker')">
                        <i class="fal fa-user-alt"></i> ໂປຮໄຟລຂອງຂ້ອຍ
                    </button>
                </div>
            </div>
        </div>
        <ul>
            <li class="sidebar-line"></li>
        </ul>
        <div class="sidebar-list-menu">
            <ul>
                <li @click="Goto_Router('home')">
                    <i class="fal fa-home-lg"></i>
                    <span>ໜ້າຫຼັກ</span>
                </li>
                <li @click="Goto_Router('about')">
                    <i class="fal fa-info-circle"></i>
                    <span>ຈັດການຂໍ້ມູນຫຼັກ</span>
                </li>
                <li @click="Goto_Router('about')">
                    <i class="fal fa-utensils-alt"></i>
                    <span>ລາຍການສັ່ງອາຫານ</span>
                </li>
                <li @click="Goto_Router('about')">
                    <i class="fal fa-clipboard-list-check"></i>
                    <span>ລາຍງານຂໍ້ມູນທັງໝົດ</span>
                </li>

                <li @click="SignOut()">
                    <i class="fal fa-sign-out-alt"></i>
                    <span>ອອກຈາກລະບົບ</span>
                </li>
             
            </ul>
        </div>
    </nav>
</template>

<script>
export default {
    data:()=>({
        item: '',
    }),
    methods:{
        closeModal(){
            this.$emit('closed');
        },
        Goto_Router(value){
            if(value == 'home'){
                this.$router.push({name: 'home'});
            }else if(value == 'about'){
                this.$router.push({name: 'about'});
            }else if(value == 'allJob'){
                this.$router.push({name: 'all.job'});
            }else if(value == 'events'){
                this.$router.push({name: 'events'});
            }else if(value == 'employerSignup'){
                this.$router.push({name: 'employer.sign.up'});
            }else if(value == 'seekerSignup'){
                 this.$router.push({name: 'seeker.sign.up'});
            }else if(value == 'employer_seekerDetail'){
                this.$router.push({name: 'employer.search.seeker'});
            }
            this.closeModal();
            // Remove-Item in LocalStorage...
            localStorage.removeItem('showLive');
        },
        SignOut(){
            this.$store.dispatch('destroyToken');
            this.closeModal();
        }
    },
    created(){
        this.item = JSON.parse(localStorage.getItem("user_profile"));
    },
    computed:{
        LoggedIn() {
            return this.$store.getters.LoggedIn
        }
    },
}
</script>

