import Vue from 'vue'
import VueRouter from 'vue-router'
import redirect from '../redirectUrl';

Vue.use(VueRouter)

let adminTypes = [];
const metas = {
    guestMeta: {
        requiresVisitor: true,
        except: adminTypes,
        ...redirect.guest
    },
    defaultMeta: {
        navFixed: true,
        hideNavFooter: true,
    },

    RestaurantAuthMeta: {
      requiresAuth: true,
      allows: adminTypes.concat(['Restaurant']),
      path: redirect.auth.path
    },
    CashierAuthMeta: {
        requiresAuth: true,
        allows: adminTypes.concat(['Cashier', 'Restaurant']),
        path: redirect.auth.path
    },
    KitchenAuthMeta: {
      requiresAuth: true,
      allows: adminTypes.concat(['Kitchen']),
      path: redirect.auth.path
    }
};

  const routes = [
  {
    path: '/about',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/',
    name: 'restaurant.sign.in',
    component: () => import(/* webpackChunkName: "about" */ '../views/Sign-in.vue'),
    meta: {
      hide_navbar: true,
    }
  },

  // Tab-manage
  {
    path: '/tab-manage',
    name: 'tab.manage',
    component: () => import(/* webpackChunkName: "about" */ '../views/Manage/Tab-Manage.vue'),
    meta:{
      ...metas.RestaurantAuthMeta
    }
  },

  {
    path: '/tab-list-order',
    name: 'tab.list.ordered',
    component: () => import(/* webpackChunkName: "about" */ '../views/List-Orders/Tab-Manage.vue'),
    meta:{
      ...metas.CashierAuthMeta,
    }
  },
]

const router = new VueRouter({
  mode: 'history',
  routes
})

export default router
