<script>
import Sidebar from '@coms/Sidebar'

    export default {
        components:{
            Sidebar
        },
        data: () => ({
            showSidebar: false,
            showDropdown: false,
            user: {},
        }),
        watch:{
            showSidebar:function(val){
                if(val == true){
                    document.body.style.overflowY = "hidden" 
                }else{
                    document.body.style.overflowY = "auto" 
                }
            },
        },
        methods:{
            SignOut(){
                this.$store.dispatch('destroyToken');
            },
            Listorders(){
                if(this.$route.query.tab == 'list_order'){
                    this.$router.push({ query: { ...this.$route.query, list: 'ordered' }}).catch(()=>{});
                }else{
                    this.$router.push({name: 'tab.list.ordered', query:{tab: 'list_order'}}).catch(()=>{});
                }
            },
            TabManage(){
                if(this.$route.query.tab == 'tab_manage'){
                    this.$router.push({ query: { ...this.$route.query, tab: 'tab_manage' }}).catch(()=>{});
                }else{
                    this.$router.push({name: 'tab.manage', query:{tab: 'tab_manage'}}).catch(()=>{});
                }
            }
        },
        created(){
            
        }
    }
</script>

