const hasActiveApp = (r, n) => {
    return r.resolve({name: n}).resolved.matched.length > 0;
};

export default function StartRouteGuard(router, store, baseLogin) {
    router.beforeEach((to, from, next) => {
        // get user type
        let userType = store.getters.getUserType;
        if (to.matched.some(record => record.meta.requiresAuth)) {
            // this route requires auth, check if logged in
            // if not, redirect to login page.
            if (!store.getters.LoggedIn) {
                if (hasActiveApp(router, baseLogin.name)) {
                    next({name: baseLogin.name})
                } else {
                    window.location.replace(baseLogin.path);
                }
            } else {
                if (to.meta.allows && to.meta.allows.includes(userType)) {
                    next();
                } else {// redirect to owner path
                    window.location.replace(to.meta.path[userType] || '/');
                }
            }
        } else if (to.matched.some(record => record.meta.requiresVisitor)) {
            if (store.getters.LoggedIn) {
                if (to.meta.except && to.meta.except.includes(userType)) {
                    next();
                    return;
                }
                let redirectRoute = to.meta.redirect[userType];
                if (hasActiveApp(router, redirectRoute)) {
                    next({name: redirectRoute})
                } else {// redirect to owner path is not logged in
                    window.location.replace(to.meta.path[userType] || '/');
                }
            } else {
                next()
            }
        } else {
            next() // make sure to always call next()!
        }
    })
}
