import Vue from "vue";
import Vuex from "vuex";
import axios from 'axios';
import router from '../router/index';


Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    token: localStorage.getItem('access_token') || null,
    msg_error_login: '',
    userProfile: {},
  },
  getters: {
    LoggedIn(state) {
        return state.token !== null;
    },
    getUserType(state) {
        let profile = window.localStorage.getItem('user_profile');
        if (profile) {
            try {
                profile = JSON.parse(profile);
            } catch (err) {
                profile = {}
            }
        } else {
            profile = {};
        }
        return state.userProfile.user_type || profile.user_type;
    },
    getToken(state) {
      return state.token;
    },
    isAuth(state) {
      return state.token && state.token !== null;
    }
  },
  mutations: {
    Restaurant_SignIn(state, token) {
      state.token = token
    },
    destroyToken(state) {
        state.token = null;
    },
    error_msg_login(state, msg){
      state.msg_error_login = msg
    },
    setUserProfile(state, payload) {
      state.userProfile = payload;
    },
  },
  actions: {
    RestaurantSignIn(context, data) {
      return new Promise((resolve, reject) => {
          axios.post('sign-in', {
              email: data.email,
              password: data.password
          })
          .then(response => {
              resolve(response)
              if(response.data.success == true){
                const token = response.data.access_token;
                localStorage.setItem('access_token', token);   // ເກັບ Token ໄວ້ໃນ Localstorage ເພື່ອຈະນຳໄປໃຊ້ຂໍຂໍ້ມູນ
                context.commit('Restaurant_SignIn', token);
                context.commit('setUserProfile', response.data.user);
                window.localStorage.setItem('user_profile', JSON.stringify(response.data.user));
                if(response.data.user.user_type == 'Restaurant'){
                  router.push({name: 'tab.manage'});
                }else if(response.data.user.user_type == 'Cashier'){
                  router.push({name: 'tab.list.ordered'});
                }
                setTimeout(()=>{
                  window.location.reload();
                }, 300);
              }else{
                context.commit('error_msg_login', response.data.msg);
                setTimeout(() => {
                  context.commit('error_msg_login', '');
                }, 3000);
              }
          })
          .catch(error => {
              reject(error)
              if(error.response.status == 401){
                  context.commit('error_msg_login', 'ອີເມວ ຫຼື ລະຫັດຜ່ານບໍ່ຖືກຕ້ອງ...');
                  setTimeout(() => {
                    context.commit('error_msg_login', '');
                  }, 3000);
              }else if(error.response.status == 400){
                  context.commit('error_msg_login', 'ອີເມວ ຫຼື ລະຫັດຜ່ານບໍ່ຖືກຕ້ອງ...');
                  setTimeout(() => {
                    context.commit('error_msg_login', '');
                  }, 3000);
              }
          })
      })
    },

    destroyToken(context) {
      if (context.getters.LoggedIn) {
          return new Promise((resolve, reject) => {
              axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
              axios.post('sign-out')
              .then(response => {
                resolve(response)
              })
              .catch((error) => {
                  reject(error)
              })
              .finally(response => {
                  resolve(response)
                  localStorage.removeItem('access_token')     // Remove Item Of Localstorage...
                  context.commit('destroyToken')
                  router.push({
                      name: 'restaurant.sign.in'
                  })
                  .catch(() => {

                  });
              })
          })
      }
    },
  },
  modules: {}
});
